@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500&display=swap');
*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;

}

.main{
  width: 100%;
  height: 100vh;
}

.top, .bottom{
  width: 100%;
  height: 50%;
}

.top{
background-color: rgb(28, 25, 25);
}

.bottom{
background-color: rgb(222, 159, 32);
}

.content{
  width: 500px;
  min-height: 150px;
  background-color: rgb(30, 40, 62);
  border-radius: 10px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.forms{
  width: 90%;
  height: 150px ;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: center;
}


.movie_input{
  width: 300px;
  height: 30px;
  background-color: transparent;
  border-radius: 3px;
border: 1.5px solid white;
margin-right: 10px;
outline: none;
color: white;
font-family: 'Poppins', sans-serif;
letter-spacing: 0.8px;
padding: 0 10px;
}

.search_btn{
  width: 85px;
  height: 30px;
  background-color: rgb(222, 159, 32);
  border-radius: 3px;
  border: none;
  font-size: 12px;
  font-weight: 500;
  cursor: pointer;
  letter-spacing: 0.8px;

}

.movie_details{
  width: 90%;
  min-height: 50px;
  margin: auto;
  padding-bottom: 30px;
}

.det_top{
display: flex
}

.det_top_right{
  width: 65%;
  height: auto;
  display: flex;
  flex-direction: column;
  align-items: center;

}

.movie_name{
color: white;
font-family: 'Poppins', sans-serif;
letter-spacing: 0.8px;
padding: 0 10px;
}

.movie_img{
  width: 35%;
}

.movie_name{
  border: none;
  text-align: center;
  font-size: 20px;
}

.movie_rate{
  border: none;
  text-align: center;
  font-size: 18px;
  color: white;
  padding: 15px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

span{
  color: #a0a0a0;
  font-size: 14px;
  padding: 20px 5px;
}

.genre_main{
  width: 100%;
  display: flex;
  justify-content: center;
  margin: 20px 0;
}

.genre{
  width: 70px;
  height: 25px;
  border: 1px solid #a0a0a0;
  border-radius: 3px;
  color: #fff;
  margin: 10px;
  display: flex;
  align-items: center;
  justify-content: center;

}

.genre > p{
font-family: 'Poppins', sans-serif;
  font-size: 11px;
}

.del_bottom{
  width: 100%;
}

.plot_main, .cast_main{
  width: 100%;
  padding: 10px;
  font-family: 'Poppins', sans-serif;
}
.plot_main > h2, .cast_main > h2{
  color: white;
  font-weight: 500;
  font-size: 18px;
}

.plot_main > P, .cast_main > P{
  color: #a0a0a0;
  font-size: 13px;
  letter-spacing: 0.2px;
  line-height: 19px;
  text-align: justify;
}

.errMsg{
  color: white;
  text-align: center;
  font-weight: 500;
  font-size: 20px;
}